@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

.calories-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 40px;
    font-family: 'Poppins', sans-serif;
    background-color: #f8f8f8;
    transition: background-color 0.3s ease;
}

.calories-content {
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
    padding: 40px;
    width: 600px;
    text-align: center;
    transition: background-color 0.3s ease;
}

.calories-header {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.calories-icon-container {
    background-color: #e67e00;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    transition: background-color 0.3s ease;
}

.calories-icon {
    color: #fff;
    font-size: 28px;
    transition: color 0.3s ease;
}

.calories-title {
    font-size: 28px;
    font-weight: 600;
    color: #333;
    transition: color 0.3s ease;
}

.calories-input {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.input-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.input-label {
    font-size: 16px;
    font-weight: 500;
    color: #666;
    margin-bottom: 8px;
    transition: color 0.3s ease;
}

.input-field {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    transition: border-color 0.3s ease;
}

.input-field:focus {
    outline: none;
    border-color: #e67e00;
}

.calories-overview {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
    margin-bottom: 40px;
}

.calories-info {
    background-color: #f5f5f5;
    border-radius: 8px;
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: background-color 0.3s ease;
}

.calories-label {
    font-size: 16px;
    font-weight: 500;
    color: #666;
    margin-bottom: 8px;
    transition: color 0.3s ease;
}

.calories-value {
    font-size: 28px;
    font-weight: 700;
    color: #333;
    transition: color 0.3s ease;
}

.calories-cta {
    display: flex;
    justify-content: center;
}

.btn-primary {
    background-color: #e67e00;
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 14px 28px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.btn-primary:hover {
    background-color: #e67e00;
    transform: translateY(-2px);
}

.dark-mode {
    background-color: #1c1c1c;
    color: #fff;
}

.dark-mode .calories-content {
    background-color: #2d2d2d;
}

.dark-mode .calories-info {
    background-color: #333;
}

.dark-mode .calories-icon,
.dark-mode .calories-label,
.dark-mode .calories-value {
    color: #fff;
}

.dark-mode .calories-title {
    color: #fff;
}

.dark-mode .input-field {
    background-color: #2d2d2d;
    color: #fff;
    border-color: #444;
}

.dark-mode .input-field:focus {
    border-color: #e67e00;
}

.dark-mode .btn-primary {
    background-color: #e67e00;
}

.dark-mode .btn-primary:hover {
    background-color: #e67e00;
}

@media (max-width: 768px) {
    .calories-input {
        grid-template-columns: 1fr;
    }
}