.health-icon {
    text-decoration: none;
    color: inherit;
    transition: transform 0.2s ease-in-out;
}

.health-icon:hover {
    transform: translateY(-5px);
}

.health-tracker-heading {
    text-align: left;
    width: 100%;
    margin-bottom: 1rem;
}

.icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 8px solid transparent;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    transition: border-color 0.2s ease-in-out;
}

.health-icon:hover .icon-container {
    border-color: #e67e00;
}

.icon {
    width: 50px;
    height: 50px;
}

.health-icon .icon-container {
    width: 105px;
    height: 105px;
}

.dark-text {
    color: #e5e7eb;
}

.light-text {
    color: #333;
}

@media (max-width: 768px) {
    .icon-container {
        width: 80px;
        height: 80px;
    }

    .icon {
        width: 40px;
        height: 40px;
    }

    .health-icon .icon-container {
        width: 70px;
        height: 70px;
    }

    .health-icon p {
        font-size: 1rem;
    }
}